import Button from "../Button/Button";
import AuthService from "../services/authService";

export default function TabsSection({active, onChange}) {
    const userAdmin = AuthService.getCurrentUser();

    return (
        <section style={{marginBottom: '1rem'}}>
            <Button isActive={active === 'main'} onClick={() => onChange('main')}></Button>
            <Button isActive={active === 'catalog'} onClick={() => onChange('catalog')}></Button>

            {/*   " ",     */}
            {(!userAdmin || userAdmin.username !== 'admin') && (
                <Button isActive={active === 'feedback'} onClick={() => onChange('feedback')}> </Button>
            )}

            {/*   " ",     */}
            {(userAdmin && userAdmin.username !== 'admin') && (
                <Button isActive={active === 'cabinet'} onClick={() => onChange('cabinet')}> </Button>
            )}

            {/*  " "    */}
            {userAdmin && userAdmin.username === 'admin' && (
                <Button isActive={active === 'admin_panel'} onClick={() => onChange('admin_panel')}> </Button>
            )}

            <Button isActive={active === 'about'} onClick={() => onChange('about')}> </Button>
        </section>
    );
}
